<template>
  <div id="char2" ref="chart" :style="{ width: '100%', height: '480px' }"></div>
</template>
  
  <script>
  //   import Echart from '@/common/echart'
  import 'echarts-wordcloud/dist/echarts-wordcloud'
  import 'echarts-wordcloud/dist/echarts-wordcloud.min.js'
  // import cloud from '../../../../assets/cloud.png'
  export default {
    data() {
      return {
        worddata: [],
      }
    },
    components: {
      //   Echart
    },
    mounted() {
      setTimeout(() => {
        this.initCharts() //初始化
      }, 0)
      this.getWordData()
    },
    methods: {
      initCharts() {
        //   console.log(this.worddata);

        let a = this.$refs.chart
        // const maskImage = new Image()
        // maskImage.src = "data:image/png;base64,"

        let myChart2 = this.$echarts.init(a)
        myChart2.setOption({
          title: {
            //text: "center"
          },
          backgroundColor: 'rgba(255, 255, 255, 0)',
          tooltip: {
            // show: true,
          },

          series: [
            {
              type: 'wordCloud',
              shape: 'pentagon',
              // keepAspect: true,
              // autoSize: {
              //   enable: true,
              //   minSize: 60,
              // },
              // maskImage: maskImage,
              //用来调整词之间的距离
              gridSize: 6,
              // //用来调整字的大小范围
              sizeRange: [15, 130],
              // //用来调整词的旋转方向，，[0,0]--代表着没有角度，也就是词为水平方向，需要设置角度参考注释内容
              rotationRange: [-90, 90],
              // rotationRange: [ 0,90],
              // rotationRange: [0, 0],
              //随机生成字体颜色
              textStyle: {
                fontFamily: 'KAITI',
                fontWeight: 'bold',
                // Color can be a callback function or a color string
                color: function () {
                  // Random color
                  let colors = [
                    '#37a2da',
                    '#32c5e9',
                    '#9fe6b8',
                    '#ffdb5c',
                    '#ff9f7f',
                    '#fb7293',
                    '#e7bcf3',
                    '#8378ea',
                    '#37a2da',
                    '#ff9f7f',
                    '#ff9f7f',
                    '#fb7293'
                  ]
                  return (
                    // 'rgb(' +
                    // [
                    //   Math.round(Math.random() * 160),
                    //   Math.round(Math.random() * 160),
                    //   Math.round(Math.random() * 160),
                    // ].join(',') +
                    // ')'
                    [colors[Math.round(Math.random() * 10)]]
                  )
                },
              },
              emphasis: {
                focus: 'self',

                textStyle: {
                  shadowBlur: 10,
                  shadowColor: '#333',
                },
              },
              //位置相关设置
              left: 'center',
              top: 'center',
              right: null,
              bottom: null,
              width: '200%',
              height: '200%',
              //数据
              data: this.worddata,
            },
          ],
        })
        //点击事件
        myChart2.on('click', function (params) {
          console.log(params)
        })
      },
      getWordData() {
        this.worddata = this.$store.getters.words
        console.log(this.$store.getters.words)
      },
    },
  }
</script>
  