<template>
  <div>
    <Echart
      id="centreLeft2Chart"
      ref="centreLeft2ChartRef"
      :options="options"
      height="360px"
      width="330px"
    ></Echart>
  </div>
</template>

<script>
import Echart from '@/common/echart';
export default {
  data() {
    return {
      options: {},
    };
  },
  components: {
    Echart,
  },
  props: {
    cdata: {
      type: Object,
      default: () => ({}),
    },
  },
  watch: {
    cdata: {
      handler(newData) {
        this.options = {
          color: [
            "#37a2da",
            "#32c5e9",
            "#9fe6b8",
            "#ffdb5c",
            "#fb7293",
            "#ff9f7f",
            "#e7bcf3",
            "#8378ea"
          ],
          tooltip: {
            trigger: "item",
          },
          toolbox: {
            show: true
          },
          calculable: true,
          legend: {
            // orient: "horizontal",
            icon: "circle",
            bottom: 30,
            x: "center",
            data: newData.xData,
            textStyle: {
              color: "#fff"
            }
          },
          series: [
            {
              name: "情感",
              type: "pie",
              radius: [20, 100],
              roseType: "area",
              center: ["50%", "40%"],
              itemStyle: {
                borderRadius: 10,
                borderColor: '#fff',
                borderWidth: 2
              },
              label: {
                fontSize: '18',
              },
              emphasis: {
              label: {
                show: true,
                fontSize: '25',
                fontWeight: 'bold'
              }
            },
              data: newData.seriesData
            }
          ]
        }

      },
      immediate: true,
      deep: true,
    },
  },
  methods: {
  },
};
</script>
