<template>
  <div>
    <Chart :cdata="cdata" />
  </div>
</template>

<script>
import Chart from './chart.vue';
export default {
  data () {
    return {
      cdata: {
        xData: [],
        seriesData: [
        ]
      }
    }
  },
  components: {
    Chart,
  },
  mounted () {
    this.getAuthorPoemsNum()
  },
  methods: {
    getAuthorPoemsNum () {
      let AuthorPoemsNum = this.$store.getters.authorPoemsNumTop7
      Object.entries(AuthorPoemsNum).forEach(([key, value]) => {
        this.cdata.xData.push(key)
        this.cdata.seriesData.push({ value: value, name: key })
      })
    }
  }
}
</script>

